////////////////////////
// CORE / OBJECTS / GRID
////////////////////////


@supports (display: grid) {

  .dcf-grid,
  .dcf-grid-full,
  .dcf-grid-full\@sm,
  .dcf-grid-full\@md,
  .dcf-grid-full\@lg,
  .dcf-grid-full\@xl,
  .dcf-grid-halves,
  .dcf-grid-halves\@sm,
  .dcf-grid-halves\@md,
  .dcf-grid-halves\@lg,
  .dcf-grid-halves\@xl,
  .dcf-grid-thirds,
  .dcf-grid-thirds\@sm,
  .dcf-grid-thirds\@md,
  .dcf-grid-thirds\@lg,
  .dcf-grid-thirds\@xl,
  .dcf-grid-fourths,
  .dcf-grid-fourths\@sm,
  .dcf-grid-fourths\@md,
  .dcf-grid-fourths\@lg,
  .dcf-grid-fourths\@xl,
  .dcf-grid-fifths\@md,
  .dcf-grid-fifths\@lg,
  .dcf-grid-fifths\@xl,
  .dcf-grid-sixths\@md,
  .dcf-grid-sixths\@lg,
  .dcf-grid-sixths\@xl {
    display: grid;
  }

  .dcf-grid {
    grid-template-columns: repeat(12, 1fr);
  }

  .dcf-grid-full {
    grid-template-columns: 1fr;
  }

  .dcf-grid-halves {
    grid-template-columns: repeat(2, 1fr);
  }

  .dcf-grid-thirds {
    grid-template-columns: repeat(3, 1fr);
  }

  .dcf-grid-fourths {
    grid-template-columns: repeat(4, 1fr);
  }

  .dcf-col-100\% {
    grid-column: 1 / span 12;
  }

  .dcf-col-50\%-start {
    grid-column: 1 / span 6;
  }

  .dcf-col-50\%-end {
    grid-column: span 6 / -1;
  }

  .dcf-col-33\%-start {
    grid-column: 1 / span 4;
  }

  .dcf-col-33\%-end {
    grid-column: span 4 / -1;
  }

  .dcf-col-67\%-start {
    grid-column: 1 / span 8;
  }

  .dcf-col-67\%-end {
    grid-column: span 8 / -1;
  }

  .dcf-col-25\%-start {
    grid-column: 1 / span 3;
  }

  .dcf-col-25\%-end {
    grid-column: span 3 / -1;
  }

  .dcf-col-75\%-start {
    grid-column: 1 / span 9;
  }

  .dcf-col-75\%-end {
    grid-column: span 9 / -1;
  }


