/////////////////////////////
// CORE / GENERIC / KEYFRAMES
/////////////////////////////


// Appear https://observablehq.com/@argyleink/a-reusable-way-to-present-fresh-dom
@keyframes appear {

  to {
    opacity: 1;
    transform: translate3d(0,0,0);
  }

}


// Disappear
@keyframes disappear {

  to {
    opacity: 0;
  }

}
